@import 'variables.scss';

.topbar {
    $paddingTB: 10px;
    $paddingLR: 20px;
    background: transparent;
    display: flex;
    flex-direction: row;
    width: calc(100% - $paddingLR * 2);
    padding: $paddingTB $paddingLR;
    min-width: 600px;
    height: calc($topbar-height - $paddingTB * 2);
    align-items: center;
    .changeTheme{
        // position: absolute;
        // top: 10px;
        // right: 10px;
        font-size: 30px;
        cursor: pointer;
        color: $btnColorDark;
        margin-left: 10px;
        max-width: 30px;
    }
}

.options {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    flex: 3 0;

    .option {
        font-family: $font-family;
        font-style: $font-style;
        font-weight: $font-weight;
        font-size: $font-size;
        line-height: $line-height;

        color: $color-black;
        cursor: pointer;
    }
}


.search {
    width: 350px;
    height: 37px;
    left: 546px;
    top: 50px;

    background: rgba(196, 196, 196, 0.4);
    backdrop-filter: blur(150px);
    border-radius: 10px;
    flex: 2 0;
    margin-left: 20px;
    z-index: 10;

    input {
        background: transparent;
        border: none;
        outline: none;
        height: 100%;
        // color: $color-white;
        color: $color-black;
        margin-left: min(5%, 10px);
    }

    input::-webkit-input-placeholder {
        color: $color-gray;
    }
    .searchList{
        position: absolute;
        top: 40px;
        left: 0;
        width: 60%;
        height: fit-content;
        // height: calc(100% - 50px);
        background: #fcbea8;
        backdrop-filter: blur(150px);
        border-radius: 10px;
        overflow: auto;
        z-index: 1;
        overflow-x: hidden;

        >span{
            padding: 10px;
            display: block;
            width: 100%;
            cursor: pointer;
            &:hover{
                background: rgba(196, 196, 196, 0.4);
            }
            overflow-x: hidden;
        }
    }
}

.topbar.dark{

    .option{
        color: $dark-title-color;
    }
    .searchList{

        background: #353d6c;

        >span{
            padding: 10px;
            display: block;
            width: 100%;
            cursor: pointer;
            &:hover{
                background: rgba(196, 196, 196, 0.4);
            }
            overflow-x: hidden;
        }
    }

    .search{
        background: rgba(0, 0, 0, 0.4);
        backdrop-filter: blur(150px);
        border-radius: 10px;
        input{
            color: $color-white;
        }
    }
}