.mainPage.dark {
    background: $backgroundColorDark;
    color: $color-white;

    .collectionNav {
        background: linear-gradient(0deg, $backgroundColorDark 0%, rgba(16, 16, 22, 1) 100%);
    }

    .passwordNav {
        background: linear-gradient(0deg, $backgroundColorDark 0%, rgba(27, 28, 39, 1) 100%);
    }

    .navItem {
        color: $color-white;

    }

    .navItem:hover {
        background: rgba(207, 207, 207, 0.35);
        backdrop-filter: blur(5px);
    }

    .navItem::after {
        content: '';
        display: block;
        width: 85%;
        margin-left: 7.5%;
        height: 1px;
        background: $btnColorDark;
        // margin-top: 10px;
    }
}

.password.dark {
    .title>input {
        color: $dark-title-color;
    }

    .inputBox>input {
        color: $color-white
    }

    .textareBox>textarea {
        color: $color-white
    }

    .btn {
        background: $btnColorDark;
        color: $color-white;
    }
}




.detail-password {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: calc(100vh - $topbar-height);
    overflow-y: auto;

    .detailInfo {
        width: 90%;
        margin-bottom: 20px;
        margin: 10px auto;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;

        >*:nth-child(1) {
            flex: 1;
            text-align: right;
        }

        >*:nth-child(2) {
            flex: 2.5;
            margin-left: 50px;
        }

        >span:nth-last-child(1) {
            display: block;
            line-height: 35px;
            height: 35px;
            border-radius: 5px;
            background: rgba(239, 239, 239, 0.048);
            box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25);
            backdrop-filter: blur(5px);
            padding: 2px 8px;
        }
        .historyPwd{
            height: 200px;
            display: block;
            line-height: 35px;
            border-radius: 5px;
            background: rgba(239, 239, 239, 0.048);
            background: rgba(239, 239, 239, 0.048);
            box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25);
            backdrop-filter: blur(5px);

            overflow-y: auto;
            padding: 2px 8px;

        }
    }
    .btns {
        display: flex;
        flex-direction: row;
        justify-content: center;
    }

    .btn {
        display: block;
        margin: 10px 20px;
        width: 80px;
        text-align: center;
        height: 40px;
        background: $btnColor;
        border-radius: 5px;
        color: $color-white;
        font-size: 15px;
        line-height: 40px;
        cursor: pointer;

        &:active {
            transform: scale(0.95);
            transition: all 0.05s ease-in-out;
            box-shadow: none;
        }
    }

}

.detail-password.dark{
    input{
        color: $color-white;
    }
    .btn {
        background: $btnColorDark;
        color: $color-white;
    }
}